/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import budgetingData from './data-bb-budgeting-http';

const budgetingDataModuleKey = 'data-bb-budgeting-http-ng';

export const budgetingDataKey = 'data-bb-budgeting-http-ng:budgetingData';

export default ng
  .module(budgetingDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(budgetingDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       budgetingData(config),
      ],
    };
  }])
  .name;



